body,
html {
  display: block;
  margin: 0;
  height: 100vh;
  overflow: hidden;
  font-family: Lato, Roboto;
}
*:fullscreen,
*:-webkit-full-screen,
*:-moz-full-screen {
  background-color: rgba(255, 255, 255, 0);
}
#root {
  height: 100vh;
}
a {
  text-decoration: none;
  color: inherit;
}
.app {
  height: 100vh;
}
